import axios from "axios";
export default Vue.extend({
  name: "Nav",
  data() {
    return {
      dropToggle1: false,
      dropToggle2: false,
      encuesta_id: null
    }
  },
  methods: {
    logout() {
      localStorage.clear();
    },
    dropdownNow(v: boolean, n: number) {
      if (n === 1) this.dropToggle1 = v;
      if (n === 2) this.dropToggle2 = v;
    }
  }
});
